
/*---------------formatter---------------*/
* {
  margin: 0px;
  padding: 0px;
}

html {
    background: #b6030356
}

body {
    background-image: -webkit-gradient(linear,
     left top, left bottom,
     from(rgb(221, 222, 223)), to(rgb(167, 167, 167)));
    background-image: -webkit-linear-gradient(top,
     rgb(221, 222, 223), rgb(167, 167, 167));
    background-image: -o-linear-gradient(top,
     rgb(221, 222, 223), rgb(167, 167, 167));
    background-image: linear-gradient(-900deg,
     rgb(221, 222, 223), rgb(167, 167, 167));
}

/*-----------------------------------------*/


.application-main {
    background-color: rgb(85, 5, 5);
    position: relative;
    top:100px;
    width:100%;
}


